<template>
  <div>
    <h1 style="color: red">Todo List</h1>
    <input type="text" ref="inp" 
    placeholder="请输入待办事项" 
    @keydown.enter="add"
    v-model="inputValue"
    maxlength="27"
    >
  </div>
</template>

<script>
import { nanoid } from 'nanoid'
export default {
  data(){
    return{
      creatdate:'',
      inputValue:''
    }
  },
  props:['addItem'],
  methods:{
    add(e){
      //将用户的行为包装成todo对象
      if(!e.target.value.trim()) return alert('输入数据不能为空')
      this.creatdate = new Date().toLocaleString()
      const todoObj = {id:nanoid(),name:e.target.value,done:false,dates:this.creatdate}
      this.addItem(todoObj)
      e.target.value = ''
    },
    getBLen(str){
        if (!str) return 0;  
          if (/[\u4E00-\u9FA5]/g.test(str)){  
              return 2;  //如果是汉字就将长度变为2
          }   
        return 1;
    },
  },
  watch:{
    inputValue(value){
      var len = 0
      value.split('').forEach( str => {
         len += this.getBLen(str)
      })
      if(len > 26){
        var arr = this.inputValue.split('')
        this.inputValue = arr.splice(0,arr.length-1).join('')
        return alert('输入字符长度过长')
      }
    }
  },
  mounted(){
    this.$refs.inp.focus()
  }
}
</script>

<style scoped>
  input{
    width: 600px;
    height: 30px;
    border-style: solid;
    border: 2px solid rgb(78,110,242);
    border-radius: 5px;
    outline-color:pink
  }
  h1{
    text-align: center;
  }
</style>