<template>
  <div>
    <div class="box1">
        <p class="time">今天是{{ date }}{{ holiday }}</p>
        <div class="text">
            <span>倒计时离</span>
            <span @click="change()" v-show="isShow">{{this.inpValue}}</span>
            <input type="text" placeholder="2023-1-1"
            v-show="!isShow" 
            v-model="inpValue"
            @blur="nofocus($event)"
            ref="inp"
            >
            <span>还有</span>
        </div>
        <div class="box2">
            <span>{{ reverseTime.days }}</span>
            <i>天</i>
            <span>{{ reverseTime.hour }}</span>
            <i>:</i>
            <span>{{ reverseTime.minute }}</span>
            <i>:</i>
            <span>{{ reverseTime.second }}</span>
        </div>
        <p class="now">现在是{{ time }}</p>
    </div>
  </div>
</template>

<script>
export default {
    data(){
        return{
            date:'',
            time:'',
            interval:'',
            isShow:'true',
            inpValue: JSON.parse(localStorage.getItem('Date')) || '2023-1-1',
            dateArr: [
                '星期日',
                '星期一',
                '星期二',
                '星期三',
                '星期四',
                '星期五',
                '星期六'
            ],
            holiday:'',
            reverseTime:{
                days:'',
                hour:'',
                minute:'',
                second:''
            }
        }
    },
    methods:{
        change(){
            this.isShow = !this.isShow
            this.$nextTick(() => {
                this.$refs.inp.focus()
            })
        },
        nofocus(e){
            this.isShow = true
            this.inpValue = e.target.value
            localStorage.setItem('Date',JSON.stringify(e.target.value))
            this.getDate1()
            this.getDate2()
            setInterval(()=>{
                this.getDate2()
                this.getDate1()
            },1000)
        },
        getDate1(){
            var now = new Date();
            var yearn = now.getFullYear();
            var monthn = now.getMonth()+1;
            var dayn = now.getDate();
            var dayhn = now.getDay();
            this.holiday = this.dateArr[dayhn]
            var hourn = now.getHours();
            hourn = hourn < 10 ? '0' + hourn : hourn
            var minutesn = now.getMinutes();
            minutesn = minutesn < 10 ? '0' + minutesn : minutesn;
            var secondn = now.getSeconds();
            secondn = secondn < 10 ? '0' + secondn : secondn
            this.date = yearn+"年"+monthn+"月"+dayn+"日"; 
            this.time = hourn+":"+minutesn+":"+secondn; 
        },
        getDate2(){
            var now = +new Date()
            var inpuValue = this.inpValue
            var future = +new Date(inpuValue)
            if(now === future || now > future){
                clearInterval(this.interval)
            }
            var count = (future - now) / 1000
            var days = parseInt(count / 60 / 60 / 24)
            this.reverseTime.days = days < 10 ? '0' + days : days
            var hour = parseInt(count / 60 / 60 % 24)
            this.reverseTime.hour = hour < 10 ? '0' + hour : hour
            var minute = parseInt(count / 60 % 60)
            this.reverseTime.minute = minute < 10 ? '0' + minute : minute
            var second = parseInt(count % 60)
            this.reverseTime.second = second < 10 ? '0' + second : second
        }
    },
    mounted(){
        this.getDate1()
        this.getDate2()
        this.interval = setInterval(()=>{
            this.getDate1()
            this.getDate2()
        },1000)
    }
}
</script>

<style scoped>
    *{
        margin: 0;
        padding: 0;
    }
    .box1{
        position: relative;
        width: 210px;
        height: 200px;
        background-color: rgb(146,30,42);
    }
    .text input{
        width: 60px;
    }
    .box2{
        position: absolute;
        top: 50%;
        margin-top: -20px;
        height: 40px;
        width: 100%;
    }
    .box2 span{
        float: left;
        margin: 0 5px;
        width: 35px;
        height: 40px;
        color: aliceblue;
        text-align: center;
        line-height: 40px;
        background-color: rgb(48,53,48);
    }
    .box2 i{
        float: left;
        color: aliceblue;
        font-style: normal;
        height: 40px;
        line-height: 40px;
    }
    .box1 .time{
        position: absolute;
        color: aliceblue;
    }
    .box1 .text{
        position: absolute;
        top: 20%;
        left: 10%;
        color: aliceblue;
    }
    .box1 .now{
        position: absolute;
        top: 80%;
        left: 20%;
        color: aliceblue;
    }
</style>