<template>
  <div>
    <div class="timer">
      <reverseTime/>
    </div>
    <div class="wrap">
      <todoHeader :addItem="addItem"/>
      <todoList :listArr="listArr" 
      :checkTodo="checkTodo"
      :delTodo="delTodo"
      />
      <todoFooter :listArr="listArr" :delAll="delAll"/>
    </div>
  </div>
</template>

<script>
import reverseTime from './components/reverse_time'
import todoHeader from './components/todo_header'
import todoList from './components/todo_list'
import todoFooter from './components/todo_footer'
export default {
  data(){
    return{
      listArr:JSON.parse(localStorage.getItem('listArr')) || []
    }
  },
  components:{
    todoHeader,
    todoList,
    todoFooter,
    reverseTime
  },
  methods:{
    addItem(data){
      this.listArr.unshift(data)
    },
    checkTodo(id){
      this.listArr.forEach( todo => { 
        if(todo.id === id)
        todo.done = !todo.done
      })
    },
    delTodo(id){
      this.listArr = this.listArr.filter(todo => todo.id !== id)
    },
    delAll(){
      this.listArr = this.listArr.filter((todo) => {
        return !todo.done
      })
    }
  },
  watch:{
    listArr:{
      deep:true,
      handler(value){
        localStorage.setItem('listArr',JSON.stringify(value))
      }
    }
  },
  mounted(){
    this.$bus.$on('updateTodo',(id,e) => {
      this.listArr.forEach(todo => {
        if(todo.id === id) todo.name = e
      })
    })
  },
  beforeDestroy(){
    this.$bus.$off('updateTodo')
  }
}
</script>

<style scoped>
  .wrap{
    width: 50%;
    margin: 0 auto;
  }
  .timer{
    float: left;
  }
</style>
