<template>
    <div class="box">
        <transition-group name="item" appear>
            <todoItem 
            v-for="obj in listArr" 
            :key="obj.id"
            :todoObj="obj"
            :checkTodo="checkTodo"
            :delTodo="delTodo"
            />
        </transition-group>
        <p v-show="!listArr.length">没有待办事项哦~</p>
    </div>
</template>

<script>
import todoItem from './todo_item.vue'
export default {
    components:{
        todoItem
    },
    props:['listArr','checkTodo','delTodo']
}
</script>

<style scoped>
.box{
    margin-top: 20px;
    width: 600px;
    height: auto;
    min-height: 100px;
    border: 2px solid orange;
    border-radius: 5px;
}
p{
    text-align: center;
}
.item-enter-active{
    animation: actives 0.5s linear;
}
.item-leave-active{
    animation: reactives 0.5s linear;
}

@keyframes actives{
    from{
        transform: translateX(-100%);
    }
    to{
        transform: translateX(0px);
    }
}
@keyframes reactives{
    from{
        transform: translateX(0px);
    }
    to{
        transform: translateX(100%);
    }
}
</style>