<template>
  <div v-show="total">
      <input type="checkbox" :checked="isAll" 
      @click="checkAll">
      <!-- @change="changeStyle($event)"> -->
      <span>已完成{{ doneNum }}/全部{{ total }}</span>
      <button @click="del">清除已完成任务</button>
  </div>
</template>

<script>
  export default {
    props:['listArr','delAll'],
    computed:{
      total(){
        return this.listArr.length
      },
      doneNum(){
        return this.listArr.reduce((pre,current) => {
          return pre + (current.done ? 1 : 0)
        },0)
      },
      isAll(){
        return this.total === this.doneNum && this.total > 0
      }
    },
    methods:{
      checkAll(e){
        this.listArr.forEach( todo => {
            todo.done = e.target.checked
        });
      },
      del(){
        this.delAll()
      },
      // changeStyle(e){
      //   this.$bus.$emit('changeStyle',e.target.checked)
      // }
    },

  }
</script>

<style scoped>
  div{
    width: 600px;
    height: 30px;
    margin-top: 10px;
    border: 2px solid salmon;
    border-radius: 5px;
    line-height: 30px;
    text-align: left;
  }
  button{
    border-radius: 5px;
    border-color: transparent;
    background-color: green;
    float: right;
    margin-top: 4px;
    color: aliceblue;
    cursor: pointer;
    margin-right: 10px;
  }
  button:hover{
    background-color: red;
  }
</style>