<template>
  <div>
    <li>
      <input
        type="checkbox"
        :checked="todoObj.done"
        @click="changeCheck(todoObj.id)"
      />
      <span v-show="!todoObj.isEdit" :class="{crossing:todoObj.done}">{{ todoObj.name }}</span>
      <input 
      v-show="todoObj.isEdit" 
      type="text" 
      :value="todoObj.name" 
      @blur="handleBlur(todoObj,$event)"
      ref="inputEdit"
      >
      <span class="dates">创建时间:{{ todoObj.dates }}</span>
      <button class="but2" @click="del(todoObj.id)">删除</button>
      <button class="but1" @click="edit(todoObj)" v-show="!todoObj.isEdit">编辑</button>
    </li>
  </div>
</template>

<script>
export default {
  data(){
    return{
    }
  },
  props: ['todoObj','checkTodo','delTodo'],
  methods: {
    changeCheck(id) {
      this.checkTodo(id);
      this.isExit = !this.isExit
    },
    del(id){
      if(confirm('确定删除吗？')){
        //通知App删除
        this.delTodo(id)
      }
    },
    edit(todoObj){
      //每次点击编辑就会给数组对象追加一个isEdit属性
      //所以我们需要一个判断逻辑，如果数组对象上没有isEdit，则追加，如果有则改为true即可
      //执行了if语句改了isEdit之后，没有走else逻辑
      //vue并没有检测到所以没有重新帮我们解析模板，页面没有更新，我们无法获取焦点
      if(Object.prototype.hasOwnProperty.call(todoObj,'isEdit')){
        todoObj.isEdit = true
      }else{
        // console.log('@');
        this.$set(todoObj,'isEdit',true)
      }
      //nextTick会在dom结点更新完毕后再执行代码
        this.$nextTick(() => {
          this.$refs.inputEdit.focus()
        })
    },
    //失去焦点回调(真正执行修改逻辑)
    handleBlur(todoObj,e){
      todoObj.isEdit = false
      if(!e.target.value.trim()) return alert('输入不能为空')
      this.$bus.$emit('updateTodo',todoObj.id,e.target.value)
      // console.log(todoObj.id,e.target.value);
    }
  }
  // mounted(){
  //   this.$bus.$on('changeStyle',(isEdit)=> {
  //     this.isExit = isEdit
  //   })
    // this.isExit = this.todoObj.done
  // },
  // beforeDestroy(){
  //   this.$bus.$off('changeStyle')
  // }
};
</script>

<style scoped>
li {
  list-style: none;
  width: 550px;
  height: 40px;
  border: 2px solid red;
  margin: 0 auto;
  margin-top: 10px;
  line-height: 40px;
  border-radius: 5px;
}
li:hover{
  background-color: skyblue;
}
li:hover button {
  display: block;
}
.dates{
  font-size: 10px;
  color: black;
  margin-left: 10px;
}
.but1 {
  float: right;
  margin-top: 8px;
  cursor: pointer;
  display: none;
  background-color: rgb(78,110,242);
}
.but2 {
  float: right;
  margin-top: 8px;
  cursor: pointer;
  display: none;
  background-color: green;
}
button{
    border-radius: 5px;
    border-color: transparent;
    float: right;
    margin-top: 4px;
    color: aliceblue;
    cursor: pointer;
    margin-right: 10px;
  }
  button:hover{
    background-color: red;
  }
  li:last-child{ 
    margin-bottom: 10px;
  }
  span{
    color: red;
  }
  .crossing{
    color: green;
    font-style:italic;
    text-decoration: line-through;
  }


</style>